import { FC } from "react";

import { Heading, StyleProps, Text, Flex } from "@chakra-ui/react";

interface Props extends StyleProps {
    title: string;
    icon?: JSX.Element;
}

export const ArticleTitle: FC<Props> = ({ title, icon, ...props }) => {
    return (
        <Flex>
            {icon && <span style={{ marginTop: 'auto', marginBottom: 'auto', marginRight: '8px'}}> {icon} </span>}

            <Heading fontSize="2xl" fontWeight="bold" {...props} data-aos="fade-down">
                {title}
            </Heading>
        </Flex>
    );
};

export const SectionTitle: FC<Props> = ({ title, ...props }) => {
    return (
        <Text 
            fontWeight="semibold" 
            fontSize="lg" {...props} 
            data-aos="fade-down"
            dangerouslySetInnerHTML={{ __html: title }}
        />
    );
};
